let SET_CURRENT_PAGE = "SET-CURRENT-PAGE"
let initialStore = {
    discus : [
        {id:1,
        theme:'Amazon rainforest plots sold via Facebook Marketplace ads', 
        userName: "Kathy",
        answers: [{user: "Jim", answer:"good"}, {user: "Jake", answer: "very good" }]},
        {id:2,
            theme:'Amazon rainforest plots sold vhhhace ads', 
            userName: "Katjjjjhy",
            answers: [{user: "Jiuuuum", answer:"goodllllll"}, {user: "Jakkkke", answer: "veWWWWry good" }]},
        {id:3,
        theme:'Amazon rketplace ads', 
        userName: "Kay",
        answers: [{user: "Jim", answer:"00d"}, {user: "Jake", answer: "very bad" }]}
        
    ]

};

const discusReducer = (state = initialStore, action) => {
    switch(action.type){
        case SET_CURRENT_PAGE:
            return{...state, currentPage: action.currentPage};
        default : return state;
    }
}

export const setCurrentNewsPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage});

export default discusReducer;