import chatbot from "../../assets/image/chatbot.png" ;
import classes from "./Discus.module.css";

const Discus = ({userName, theme, answers, ...props}) => {
    console.log(answers);
    let answersItem = answers.map(item => {
        return <div>
             <b>{item.user}:</b> {item.answer}
        </div>
    })
        return <div className={classes.wrapper} >
        <img className={classes.picture} src={chatbot} />
        <h4 className={classes.name}  >{userName}</h4>
        <h4 className={classes.theme}  >{theme}</h4>

        <a href={"#popup1"} ><div className={classes.addBtn} >ANSWERS</div></a>

        <div id="popup1" class={classes.overlay}>
            <div class={classes.popup}>
                <h2>Answers are:</h2>
                <a class={classes.close}  href="#">&times;</a>
                <div class={classes.content}>
                    {answersItem}
                </div>
            </div>
        </div> 
    </div>
}
export default Discus;