import Preloader from '../common/Preloader/Preloader';
import Discus from './Discus';
import classes from './Discus.module.css';




const Discussions =({discus, ...props}) => {
    if(!discus){
        return <Preloader />
    }

    let discusItems = discus.map( item => 
                <Discus userName={item.userName} theme={item.theme} answers={item.answers} /> )
    
    return <div>
        {discusItems}
    </div>
}
export default Discussions;